// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

on('submit', '#account-redemption-lookup', event => {
  const form = event.currentTarget as HTMLFormElement
  const input = form.querySelector<HTMLInputElement>('input[type=text]')!
  form.action = encodeURI(`/biztools/${input.value}/billing/redemptions`)
})

on('submit', '#account-opensource-lookup', event => {
  const form = event.currentTarget as HTMLFormElement
  const input = form.querySelector<HTMLInputElement>('input[type=text]')!
  form.action = encodeURI(`/biztools/${input.value}/opensource`)
})
