// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {remoteForm} from '@github/remote-form'

// TODO Replace with data-replace-remote-form behavior.
remoteForm('.js-feature-listing-form', async function (form, wants) {
  const response = await wants.html()

  if (response) {
    const container = form.closest<HTMLElement>('.js-listing-container')!
    container.replaceWith(response.html)
  }
})

on('change', '.js-select-rejection-reason', event => {
  const input = event.target as HTMLInputElement
  if (input.name !== 'reason') return
  const deleteButton = document.querySelector<HTMLButtonElement>('.js-update-rejection-reason-btn')!
  const helperTextDiv = document.querySelector<HTMLDivElement>('.js-disable-button-message')!
  const noReasonErrorMessage = helperTextDiv.getAttribute('data-no-reason-error-message')!
  const emptyTextErrorMessage = helperTextDiv.getAttribute('data-empty-error-message')!
  let disabled = input.value === 'none'
  if (input.value === 'CUSTOM_REASON') {
    const customReasonTextarea = document.querySelector<HTMLTextAreaElement>('.js-custom-reason-textarea')!
    const value = customReasonTextarea.value
    disabled = !value.trim()
  }
  deleteButton.disabled = disabled
  if (disabled) {
    if (input.value === 'CUSTOM_REASON') helperTextDiv.textContent = emptyTextErrorMessage
    else helperTextDiv.textContent = noReasonErrorMessage
  } else {
    helperTextDiv.textContent = ''
  }
})

on('change', '.js-custom-reason-textarea', event => {
  const input = event.target as HTMLInputElement
  if (input.name !== 'custom_reason') return
  const dropDown = document.querySelector<HTMLInputElement>('.js-select-rejection-reason')!
  const deleteButton = document.querySelector<HTMLButtonElement>('.js-update-rejection-reason-btn')!
  const helperTextDiv = document.querySelector<HTMLDivElement>('.js-disable-button-message')!
  const noReasonErrorMessage = helperTextDiv.getAttribute('data-no-reason-error-message')!
  const emptyTextErrorMessage = helperTextDiv.getAttribute('data-empty-error-message')!
  if (dropDown.value === 'none') {
    deleteButton.disabled = true
    helperTextDiv.textContent = noReasonErrorMessage
  } else if (dropDown.value === 'CUSTOM_REASON') {
    deleteButton.disabled = !input.value.trim()
    helperTextDiv.textContent = deleteButton.disabled ? emptyTextErrorMessage : ''
  } else {
    deleteButton.disabled = false
    helperTextDiv.textContent = ''
  }
})

on('click', '.js-remove-reject-verification', event => {
  const buttonElement = event.target as HTMLButtonElement
  const removing = buttonElement.getAttribute('data-removing') === 'true'
  const submitButton = document.querySelector<HTMLInputElement>('.js-update-rejection-reason-btn')!
  submitButton.value = removing ? 'Remove verification' : 'Reject verification'
  submitButton.disabled = true
  document.querySelector<HTMLInputElement>('.js-custom-reason-textarea')!.textContent = ''
  const helperTextDiv = document.querySelector<HTMLElement>('.js-disable-button-message')!
  helperTextDiv.textContent = helperTextDiv.getAttribute('data-no-reason-error-message')
  document.querySelector<HTMLElement>('.js-remove-reject-status')!.textContent = removing ? 'remove' : 'reject'
  document.querySelector<HTMLButtonElement>('.js-actual-remove-reject-button')!.click()
})
