import {addThrottledInputEventListener} from '../throttled-input'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {remoteForm} from '@github/remote-form'
import {replaceState} from '@github-ui/browser-history-state'

remoteForm('.js-update-explore-item-form', async function (form, wants) {
  const submitButton = form.querySelector<HTMLButtonElement>('button[type="submit"]')!

  let response
  try {
    response = await wants.html()
    submitButton.classList.remove('btn-danger')
  } catch (error) {
    submitButton.classList.add('btn-danger')
  }

  if (response) {
    const container = form.closest<HTMLElement>('.js-explore-item-container')!
    container.replaceWith(response.html)
  }
})

observe('.js-curated-explore-items-query', {
  constructor: HTMLInputElement,
  initialize(filterInput) {
    addThrottledInputEventListener(filterInput, async function () {
      const containerId = filterInput.getAttribute('aria-owns')
      if (!containerId) return
      const container = document.getElementById(containerId)
      if (!container) return

      const text = filterInput.value.trim()
      const url = new URL(filterInput.getAttribute('data-url')!, window.location.origin)
      url.searchParams.set('q', encodeURIComponent(text))

      container.setAttribute('aria-busy', 'true')
      const response = await fetch(url.toString())
      container.innerHTML = await response.text()
      replaceState(null, '', url.toString())
      container.setAttribute('aria-busy', 'false')
    })
  },
})

function updateSelectedExploreItemsCount(form: HTMLFormElement, incrementCurrentCount: boolean) {
  const countEl = form.querySelector<HTMLElement>('.js-explore-item-import-count')!
  const currentCount = parseInt(countEl.textContent || '')
  const importButton = form.querySelector<HTMLButtonElement>('.js-explore-item-import-button')!
  let newCount = currentCount
  let suffix

  if (incrementCurrentCount) {
    newCount = currentCount + 1
  } else {
    newCount = currentCount - 1
  }

  if (newCount === 1) {
    suffix = countEl.getAttribute('data-suffix-singular')!
  } else {
    suffix = countEl.getAttribute('data-suffix-plural')!
  }

  importButton.disabled = newCount < 1
  countEl.textContent = `${newCount} ${suffix}`
}

function updateExploreItemImportSummary(form: HTMLFormElement, addingNewExploreItem: boolean, exploreItemName: string) {
  const summaryEl = form.querySelector<HTMLElement>('.js-explore-item-import-summary')!
  const separator = ', '
  const currentExploreItems = (summaryEl.textContent || '')
    .split(separator)
    .filter(exploreItem => exploreItem.length > 0)
  let newExploreItems

  if (addingNewExploreItem) {
    newExploreItems = currentExploreItems.concat([exploreItemName])
  } else {
    const index = currentExploreItems.indexOf(exploreItemName)
    const head = currentExploreItems.slice(0, index)
    const tail = currentExploreItems.slice(index + 1, currentExploreItems.length)
    newExploreItems = head.concat(tail)
  }

  summaryEl.textContent = newExploreItems.sort().join(separator)
}

on('change', '.js-import-explore-item-checkbox', function (event) {
  const checkbox = event.target as HTMLInputElement
  const form = checkbox.closest<HTMLFormElement>('form')!
  updateSelectedExploreItemsCount(form, checkbox.checked)
  updateExploreItemImportSummary(form, checkbox.checked, checkbox.value)
})
