// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

on('focusin', '#apply-coupon', function () {
  /* eslint-disable-next-line github/no-d-none */
  document.querySelector<HTMLElement>('.js-sttng-coupons')!.classList.remove('d-none')
})

on('click', '.js-sttng-coupons .js-coupon', function (event) {
  const target = event.currentTarget

  document.querySelector<HTMLInputElement>('#apply-coupon')!.value = target.getAttribute('data-code')!
  /* eslint-disable-next-line github/no-d-none */
  document.querySelector<HTMLElement>('.js-sttng-coupons')!.classList.add('d-none')
})
